@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
body {
  touch-action: none;
  margin: auto;
  height: 100vmin;
  width: 100vmin;
}

.fill-parent {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.top-margin {
  margin-top: 50px;
}

footer {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}
.drum-machine__container {
  background-color: #444;
  height: 90%;
  width: 80%;
  margin: auto;
  border-radius: 3%;
}

.display__container {
  width: 80%;
  height: 15%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.display {
  width: 90%;
  height: 50%;
  margin: auto;
  background-color: #131;
  color: #5a5;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.display-text {
  font-family: 'VT323', monospace;
  text-transform: uppercase;
  text-align: center;
  font-size: 5vmin;
  margin: 0;
}
.pads__container {
  height: 85%;
  width: 85%;
  margin: auto;
  flex-direction: row;
  flex-wrap: wrap;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.single-pad__container {
  height: 30%;
  width: 30%;
}

.drum-pad {
  height: 90%;
  width: 90%;
  margin: auto;
  background-color: darkgrey;
  border-radius: 10%;
  cursor: pointer;
  -webkit-animation-duration: .05s;
          animation-duration: .05s;
}

@-webkit-keyframes bounceIn {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
  
}

@keyframes bounceIn {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
  
}

.bounceIn {
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-name: bounceIn;
          animation-name: bounceIn;
}
.drum-pad p {
  font-family: 'Black Ops One',sans-serif;
  font-size: 5vmin;
}
